"use strict";
const loginForm = document.querySelector('#sign-in');
loginForm?.addEventListener("submit", function (event) {
    localStorage.setItem('id', (Math.floor(Math.random() * 1000000)).toString());
    const email = document.querySelector('#email');
    const emailValue = email?.value;
    const clientEmail = emailValue !== undefined ? emailValue.toString() : '';
    const id = localStorage.getItem('id');
    if (id !== null) {
        localStorage.setItem(id, clientEmail);
    }
    const login = document.querySelector('button[type="submit"]');
});
